import React from "react";
import "./controls.css"


const Controls = (props) => {
    return (
        <div>
            <h3 className={`card-header text-center bg-dark  text-light`}>Eth-A-Sketch app</h3>

            <div className={`d-flex justify-content-center bg-dark p-2`}>
                <ul className={`nav nav-pills flex-column flex-md-row `}>
                    <li className={`nav-item`}>
                        <button
                            className={`btn btn-warning text-white ${props.colourMode === "grayscale" ? "active" : ""
                                } `}
                            onClick={() => {
                                props.setColorMode("grayscale");
                            }}
                        >
                            Grayscale
                        </button>
                    </li>
                    <li className={`nav-item`}>
                        <button
                            className={`btn btn-warning text-white ${props.colourMode === "rainbow" ? "active" : ""
                                } `}
                            onClick={() => {
                                props.setColorMode("rainbow");
                            }}
                        >
                            Rainbow
                        </button>
                    </li>
                    <li className={`nav-item`}>
                        <button
                            className={`btn btn-warning text-white ${props.colourMode === "reset" ? "active" : ""
                                } `}
                            onClick={() => {
                                props.setColorMode("reset");
                            }}>
                            reset
                        </button>
                    </li>
                    <li className={`nav-item`}>
                        <button
                            className={`btn btn-warning text-white ${props.colourMode === "clear" ? "active" : ""
                                } `}
                            onClick={() => {
                                props.setColorMode("clear");
                            }}>
                            clear
                        </button>
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default Controls;
