
import Controls from "./Components/Controls/controls";
import Sketchpad from "./Components/Sketchpad/sketchpad";
import { useState, useEffect } from "react";
import "./App.css";

const INIT_GRID_SIZE = 32;
const INIT_BG_COLOUR = "#dbdbdb";
const INIT_PEN_COLOUR = "#383838";
const INIT_COLOUR_MODE = "grayscale";



const rainbow_colors = [
  "red",
  "orange",
  "yellow",
  "green",
  "blue",
  "indigo",
  "violet",
];



function App() {
  const [colourMode, setColourMode] = useState(INIT_COLOUR_MODE);

  const [pixelData, setPixelData] = useState([]);

  let [gridSize, setGridSize] = useState(INIT_GRID_SIZE);

  const [bgColour, setBgColour] = useState(INIT_BG_COLOUR);

  const [penColour, setPenColour] = useState(INIT_PEN_COLOUR);

  const [mouseOver, setMouseOver] = useState(false);

  const [portSize, setPortSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  window.onresize = () => {
    setPortSize({ width: window.innerHeight, height: window.innerWidth })
  };

  const initPixelData = (size, colour) => {
    const newPixelData = new Array(size ** 2);
    for (let i = 0; i < newPixelData.length; i++) {
      newPixelData[i] = colour;
    }
    return newPixelData;
  };

  const updatePixelData = (idx) => {
    const newPixelData = [...pixelData];

    newPixelData[idx] =
      colourMode === "rainbow"
        ? rainbow_colors[Math.floor(Math.random() * 6)]
        : penColour;
    setPixelData(newPixelData);
  };

  useEffect(() => {
    const newPixelData = initPixelData(INIT_GRID_SIZE, bgColour);
    setPixelData(newPixelData);
  }, [bgColour]);

  const setSizeHandler = (size) => {
    setGridSize(size);
    const newPixelData = initPixelData(size, bgColour);
    setPixelData(newPixelData);
  };

  const mouseOverHandler = (e, key) => {
    e.preventDefault();
    setMouseOver(true);
    const newPixelData = pixelData;
    newPixelData[key] = penColour;
    setPixelData(newPixelData);
  };


  const setCellColorHandler = (key) => {
    if (mouseOver) {
      updatePixelData(key);
    }
  };


  function changeColourMode(newMode) {
    setColourMode(newMode);
    let newPixelData;
    if (newMode === "grayscale") {
      setPenColour(INIT_PEN_COLOUR);
      setBgColour(INIT_BG_COLOUR);
      newPixelData = initPixelData(gridSize);
    }

    if (newMode === "rainbow") {
      setPenColour(rainbow_colors);
      setBgColour(INIT_BG_COLOUR);
      newPixelData = initPixelData(gridSize);
    }

    if (newMode === "clear") {
      setBgColour(INIT_BG_COLOUR)
      setPenColour(INIT_BG_COLOUR)
      newPixelData = initPixelData(gridSize, INIT_BG_COLOUR);
    }
    if (newMode === "reset") {

      let gridSize = prompt("Please enter your number");
      setGridSize(gridSize);
      newPixelData = initPixelData(gridSize, INIT_BG_COLOUR);
      while (isNaN(gridSize) || gridSize > 100 || gridSize < 16) {
        alert("choose betweem 16-100! only numbers!");
        gridSize = prompt("Please enter your number");
      }
    }
    setPixelData(newPixelData);

  }

  return (
    <div className="App">

      <Controls
        setSize={setSizeHandler}
        size={gridSize}
        penColor={penColour}
        backgroundColour={bgColour}
        colourMode={colourMode}
        setColorMode={changeColourMode}
      />
      <Sketchpad
        pixelData={pixelData}
        size={gridSize}
        bgColour={bgColour}
        mouseOver={mouseOverHandler}
        setColor={setCellColorHandler}
        portSize={portSize}
      />
      <div>
        <footer className={`page-footer text-center bg-dark text-light`}>
          <p> Â© 2021 Copyright
            <br />
            designed by niloofar </p>
        </footer>
      </div>


    </div>
  );
}

export default App;
